import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import DashboardImg from "../assets/images/dashboard1.png";
import axios from 'axios';
import Sidebar from "../components/Sidebar/Sidebar";
import Switch, { SwitchClassKey, SwitchProps } from '@material-ui/core/Switch';
import { withStyles, Theme, createStyles } from '@material-ui/core/styles';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';

import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css

require('dotenv').config()

const WelcomeContainer = styled.div`
  width: auto;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 16rem;
  padding: 1rem;
  font-weight: 700;
  font-size: 2rem;
  background: url(${DashboardImg}) center/cover no-repeat border-box;
`;
const ContentWrapper = styled.div`
  height: 100%;
  width: auto;
  color: #27af31;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  h1 {
    margin-top:15%;
    color: #40b65f;
  }
  
`;
const InfoButtonWrapper = styled.div`
  height: 100%;
  width: auto;
  color: #27af31;
  display: flex;
  font-weight:300;
  font-size:1rem;
  text-align:center;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  h5 {
    color: #40b65f;
  }
  .button{
    margin-top:-30%;
  }
`;

const CardWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
`;

const useStyles = makeStyles({
  root: {
    maxWidth: 375,
    minWidth: 275,
    margin: "0 1rem",
    background:
      "linear-gradient(180deg,rgba(203, 203 ,203,0.8),rgba(34 ,176, 71,1))",
  },
  bullet: {
    display: "inline-block",
    margin: "0 2px",
    transform: "scale(0.8)",
  },
  title: {
    fontSize: 25,
    color: "#282828",
    fontWeight: 700,
  },
  pos: {
    marginBottom: 12,
  },
});

const IOSSwitch = withStyles((theme) => ({
  root: {
    width: 42,
    height: 26,
    padding: 0,
    margin: theme.spacing(1),
    
  },
  switchBase: {
    padding: 1,
    '&$checked': {
      transform: 'translateX(16px)',
      color: theme.palette.common.white,
      '& + $track': {
        backgroundColor: '#52d869',
        opacity: 1,
        border: 'none',
      },
    },
    '&$focusVisible $thumb': {
      color: '#52d869',
      border: '6px solid #fff',
    },
  },
  thumb: {
    width: 24,
    height: 24,
  },
  track: {
    borderRadius: 26 / 2,
    border: `1px solid ${theme.palette.grey[400]}`,
    backgroundColor: theme.palette.grey[50],
    opacity: 1,
    transition: theme.transitions.create(['background-color', 'border']),
  },
  checked: {},
  focusVisible: {},
}))(({ classes, ...props }) => {
  return (
    <Switch
      focusVisibleClassName={classes.focusVisible}
      disableRipple
      classes={{
        root: classes.root,
        switchBase: classes.switchBase,
        thumb: classes.thumb,
        track: classes.track,
        checked: classes.checked,
      }}
      {...props}
    />
  );
});
const Welcome = () => {
  const classes = useStyles();
  const [offer, setOffer] = useState(null);
  const [menu, setMenu] = useState(null);
  
  const url = "http://"+window.location.hostname + "/offer";
  const menu_url = "http://"+window.location.hostname+ "/menu";
  const status_url = "http://"+window.location.hostname+ "/status/1";

  const [status, setStatus] = useState(null);

  const fetchStatus = async () => {
    axios.get(status_url).then((response) => {
      if (response.data) {
       
        if(response.data[0].status === 'open'){
        setStatus(true);
        }
      } else {
        setStatus(false);
      }
    });
  };

  const handleChange = (event) => {
    let status_value = null;
    if(event.target.checked == true){
      status_value = "open"
      setStatus(true);
    }else{
      status_value = "close"
      setStatus(false);
    }
    axios.put(status_url, { status:status_value})
    .then((response) => {

      fetchStatus();
      confirmAlert({
        title: 'Response messge',
        message: response.data,
        buttons: [
          {
            label: 'Okay',
            onClick: () => null
          }
         
        ]
      });
      
     
    })
    .catch((error) => {
     
      console.log(error.message);
    });
  };

 

  const fetchOffer = async () => {
    axios.get(url).then((response) => {
      if (response.data) {
        setOffer(response.data.length);
      } else {
      }
    });
  };

  const fetchMenu = async () => {
    axios.get(menu_url).then((response) => {
      if (response.data) {
        setMenu(response.data.length);
      } else {
      }
    });
  };


  useEffect(() => {
    fetchMenu();
    fetchOffer();
    fetchStatus();
  },[1]);

  return (
    <>
      <Sidebar />
      <WelcomeContainer>
        <ContentWrapper>
          <h1>Welcome to MOUNTNEPAL</h1>
          <InfoButtonWrapper>
      
          
            <FormGroup className ="button">
            {status ? (
       "restaurant is open"
      ) : (
       "restaurant is closed"
      )}
           
              <FormControlLabel
                control={<IOSSwitch checked={status} onChange={handleChange} name="checked" />}
                
                label="open/close"
              />
            </FormGroup>
            
          </InfoButtonWrapper>
          <CardWrapper>
            <Card className={classes.root}>
              <CardContent>
                <Typography
                  className={classes.title}
                  color="textPrimary"
                  gutterBottom
                >
                  Our Menu
              </Typography>
                <Typography className={classes.pos} color="textSecondary">
                  Total
              </Typography>
                <Typography variant="body1" component="p">
                  {menu} Items
                <br />
                </Typography>
              </CardContent>
            </Card>

            <Card className={classes.root}>
              <CardContent>
                <Typography
                  className={classes.title}
                  color="textPrimary"
                  gutterBottom
                >
                  Offers
              </Typography>
                <Typography className={classes.pos} color="textSecondary">
                  Total
              </Typography>
                <Typography variant="body1" component="p">
                  {offer} Items
                <br />
                </Typography>
              </CardContent>
            </Card>
          </CardWrapper>
        </ContentWrapper>
      </WelcomeContainer>
    </>
  );
};

export default Welcome;
